  --red: #b388dd;
  --red2: #52247f;
  --mf_font_size_h1_min: var(--font_base_unit) * 24;
  /* 48px */
  --mf_font_size_h2_min: var(--font_base_unit) * 20;
  /* 40 px */
  --mf_font_size_h3_min: var(--font_base_unit) * 16;
  /* 32px */
  --mf_font_size_h4_min: var(--font_base_unit) * 12;
  /* 24px */
  --mf_font_size_h5_min: var(--font_base_unit) * 9;
  /* 18px */
  --mf_font_size_h1_max: var(--font_base_unit) * 55;
  /* 110px */
  --mf_font_size_h2_max: var(--font_base_unit) * 40;
  /* 80px */
  --mf_font_size_h3_max: var(--font_base_unit) * 24;
  /* 48px */
  --mf_font_size_h4_max: var(--font_base_unit) * 16;
  /* 32px */
  --mf_font_size_h5_max: var(--font_base_unit) * 12;
  /* 24px */
  --mf_font_size_h1: clamp(
  	var(--mf_font_size_h1_min),
  	7.159vw + 1.148rem,
  	var(--mf_font_size_h1_max)
  );
  --mf_font_size_h2: clamp(
  	var(--mf_font_size_h2_min),
  	4.619vw + 1.305rem,
  	var(--mf_font_size_h2_max)
  );
  --mf_font_size_h3: clamp(
  	var(--mf_font_size_h3_min),
  	1.848vw + 1.522rem,
  	var(--mf_font_size_h3_max)
  );
  --mf_font_size_h4: clamp(
  	var(--mf_font_size_h4_min),
  	0.924vw + 1.261rem,
  	var(--mf_font_size_h4_max)
  );
  --mf_font_size_h5: clamp(
  	var(--mf_font_size_h5_min),
  	0.693vw + 0.946rem,
  	var(--mf_font_size_h5_max)
  );
  --mf_font_size_h6: clamp(
  	var(--mf_font_size_h5_min),
  	0.693vw + 0.946rem,
  	var(--mf_font_size_h5_max)
  );
  --font_base_unit: 0.125rem;
  --mf_font_size_d1_min: var(--font_base_unit) * 24;
  /* 48px */
  --mf_font_size_d2_min: var(--font_base_unit) * 30;
  /* 60px */
  --mf_font_size_d1_max: var(--font_base_unit) * 75;
  /* 150px */
  --mf_font_size_d2_max: var(--font_base_unit) * 100;
  /* 200px */
  --mf_font_size_d1: clamp(
  	var(--mf_font_size_d1_min),
  	11.778vw + -0.048rem,
  	var(--mf_font_size_d1_max)
  );
  --mf_font_size_d2: clamp(
  	var(--mf_font_size_d2_min),
  	16.166vw + -0.433rem,
  	var(--mf_font_size_d2_max)
  );
  --mf_font_size_t1_min: var(--font_base_unit) * 13;
  /* 26px */
  --mf_font_size_t2_min: var(--font_base_unit) * 10;
  /* 20px */
  --mf_font_size_t3_min: var(--font_base_unit) * 9;
  /* 18px */
  --mf_font_size_t1_max: var(--font_base_unit) * 23;
  /* 46px */
  --mf_font_size_t2_max: var(--font_base_unit) * 20;
  /* 40px */
  --mf_font_size_t3_max: var(--font_base_unit) * 16;
  /* 32px */
  --mf_font_size_t1: clamp(
  	var(--mf_font_size_t1_min),
  	2.309vw + 1.027rem,
  	var(--mf_font_size_t1_max)
  );
  --mf_font_size_t2: clamp(
  	var(--mf_font_size_t2_min),
  	2.309vw + 0.652rem,
  	var(--mf_font_size_t2_max)
  );
  --mf_font_size_t3: clamp(
  	var(--mf_font_size_t3_min),
  	1.617vw + 0.707rem,
  	var(--mf_font_size_t3_max)
  );
}
